html {
  font-family: Cinzel, sans-serif;
}

.page {
  &:has(.page__menu:target) {
    overflow: hidden;
  }

  font-size: 16px;
  color: $mainColor;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: transform $effectDuration;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
